export function showTableDiv() {
    document.getElementById("table-open").style.display = "none"
    document.getElementById("table-close").style.display = "block"
    document.getElementById("table-body").style.display = "block"
}

export function hideTableDiv() {
    document.getElementById("table-open").style.display = "block";
    document.getElementById("table-close").style.display = "none";
    document.getElementById("table-body").style.display = "none";
}

